import React from 'react'

function ProjectsSection() {
    return (
        <section id="projects">
            <div className="Section-Title Invisible" data-animation="slide-in-left">PROJECTS</div>
            <div className="Section-Title-Bar Invisible" data-animation="slide-in-right" style={{ background: "white" }}></div>
            <div className="row">
                <div className="col-xs-1"></div>
                <div className="col-xs-10 Projects-Disclaimer Invisible" data-animation="fade-in">
                    While I can't show my professional projects so far due to their non-disclosure agreements, here's a few of my side projects :
                </div>
                <div className="col-xs-1"></div>
            </div>
            <div className="Projects-Showcases">
                <div className="row">
                    <div className="col-md-7 col-xs-12 Showcase-Image-Wrapper Invisible" data-animation="slide-in-left">
                        <img className="Showcase-Image" src="media/Showcase0.png" alt="Investement Tracker"></img>
                    </div>
                    <div className="col-xs-1 HideIfMdMin"></div>
                    <div className="col-xs-10 col-md-4 Showcase-Description Invisible" data-animation="slide-in-right">
                        <div className="Project-Title">2020</div>
                        <div className="Project-Title">Investement Tracker</div>
                        <div className="Project-Description">Web/mobile application that keeps track of active investments, currently stocks, ETFs and cryptocurrencies.</div>
                        <div><span className="Pink">Frontend stack :</span>&nbsp;React + TypeScript, React Native, Redux</div>
                        <div><span className="Pink">Backend stack :</span>&nbsp;Node.js (Express), AWS, DynamoDB</div>
                    </div>
                    <div className="col-md-1 col-xs-1"></div>
                </div>
                <div className="row">
                    <div className="col-md-7 col-xs-12 Showcase-Image-Wrapper Invisible" data-animation="slide-in-left">
                        <img className="Showcase-Image" src="media/Showcase1.png" alt="HIIT Timer"></img>
                    </div>
                    <div className="col-xs-1 HideIfMdMin"></div>
                    <div className="col-xs-10 col-md-4 Showcase-Description Invisible" data-animation="slide-in-right">
                        <div className="Project-Title">2019</div>
                        <div className="Project-Title">HIIT Timer</div>
                        <div className="Project-Description">Web/mobile application specifically tailored for HIIT physical workouts, with favorite profiles and performance tracking.</div>
                        <div><span className="Pink">Frontend stack :</span>&nbsp;Angular, Ionic, PWA</div>
                        <div><span className="Pink">Backend stack :</span>&nbsp;Node.js (Express), Heroku, MongoDB</div>
                    </div>
                    <div className="col-md-1 col-xs-1"></div>
                </div>
                <div className="row">
                    <div className="col-md-7 col-xs-12 Showcase-Image-Wrapper Invisible" data-animation="slide-in-left">
                        <img className="Showcase-Image" src="media/Showcase2.png" alt="TamaList"></img>
                    </div>
                    <div className="col-xs-1 HideIfMdMin"></div>
                    <div className="col-xs-10 col-md-4 Showcase-Description Invisible" data-animation="slide-in-right">
                        <div className="Project-Title">2019</div>
                        <div className="Project-Title">TamaList</div>
                        <div className="Project-Description">Mobile/tablet application that fuses a TODO list with a tamagochi.<br /></div>
                        <div><span className="Pink">Frontend stack :</span>&nbsp;Angular, Ionic, Capacitor</div>
                        <div><span className="Pink">Backend stack :</span>&nbsp;Node.js (Express), Heroku, MongoDB</div>
                    </div>
                    <div className="col-md-1 col-xs-1"></div>
                </div>
                <div className="row">
                    <div className="col-md-7 col-xs-12 Showcase-Image-Wrapper Invisible" data-animation="slide-in-left">
                        <img className="Showcase-Image" src="media/Showcase3.png" alt="TamaList"></img>
                    </div>
                    <div className="col-xs-1 HideIfMdMin"></div>
                    <div className="col-xs-10 col-md-4 Showcase-Description Invisible" data-animation="slide-in-right">
                        <div className="Project-Title">2018</div>
                        <div className="Project-Title">Fortnite Quiz</div>
                        <div className="Project-Description">Mobile/tablet mini game about popular Fortnite memes.<br />About 100k downloads with no marketing thanks to specific SEO.</div>
                        <div><span className="Pink">Frontend stack :</span>&nbsp;Angular, Ionic, Cordova</div>
                        <div><span className="Pink">Backend stack :</span>&nbsp;Node.js (Express), Heroku, MongoDB</div>
                    </div>
                    <div className="col-md-1 col-xs-1"></div>
                </div>
            </div>
        </section>
    )
}

export default ProjectsSection