import React from 'react'

function AboutSection() {
    return (
        <section id="about">
            <div className="Section-Title Invisible" data-animation="slide-in-left">ABOUT</div>
            <div className="Section-Title-Bar Invisible" data-animation="slide-in-right" style={{ background: "#252934" }}></div>
            <div id="self-description-2" className="row">
                <div className="col-xs-12 col-md-1"></div>
                <div className="col-xs-12 col-md-3 Description-Portrait-Wrapper">
                    <img src="media/photo.png" alt="me" style={{ height: "200px" }}></img>
                </div>
                <div className="col-xs-1 HideIfMdMin"></div>
                <div className="col-xs-10 col-md-6 Description-Text Invisible" data-animation="fade-in">
                    I work as a full stack web developer at <a href="https://www.thalesgroup.com" target="blank">Thales Services</a> in Sophia-Antipolis (France),
                    where I work on high profile web applications with a focus on frontend development and user experience.
                    When I don't work, I spend time playing music or experimenting with innovative technologies.
                </div>
                <div className="col-xs-1 col-md-2"></div>
            </div>
            <div id="skills" className="row">
                <div className="col-xs-1 col-md-2"></div>
                <div className="col-xs-11 col-md-4 Invisible" data-animation="slide-in-left">
                    <div className="Top-Skills Hard-Skills">Top Hard Skills</div>
                    <div className="Top-Skills-List row">
                        <div className="col-xs-6">JavaScript, TypeScript</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        <div className="col-xs-6">HTML, CSS</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        <div className="col-xs-6">Angular 2+</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        <div className="col-xs-6">React, Redux</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/4-Stars.png" alt="star"></img></div>
                        <div className="col-xs-6">Node.js</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/4-Stars.png" alt="star"></img></div>
                        <div className="col-xs-6">Java, Golang</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/3-Stars.png" alt="star"></img></div>
                        <div className="col-xs-6">PostgreSQL, MongoDB</div>
                        <div className="col-xs-6 Skill-Rating-Wrapper"><img className="Skill-Rating" src="media/3-Stars.png" alt="star"></img></div>
                    </div>
                </div>
                <div className="col-xs-1 HideIfMdMin"></div>
                <div className="col-xs-11 col-md-5 Invisible" data-animation="slide-in-right">
                    <div className="Top-Skills Soft-Skills">Top Soft Skills</div>
                    <div className="Top-Skills-List row">
                        <div className="col-xs-12 row Soft-Skill-Row">
                            <div className="col-xs-6" style={{ padding: 0 }}>Communication</div>
                            <div className="col-xs-6 Skill-Rating-Wrapper" style={{ padding: 0 }}><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        </div>
                        <div className="col-xs-10 Soft-Skill-Description">
                            I'm not afraid to ask questions and talk to people, while adapting my vocabulary to who I'm talking to.
                        </div>
                        <div className="col-xs-2"></div>

                        <div className="col-xs-12 row Soft-Skill-Row">
                            <div className="col-xs-6" style={{ padding: 0 }}>Teamwork</div>
                            <div className="col-xs-6 Skill-Rating-Wrapper" style={{ padding: 0 }}><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        </div>
                        <div className="col-xs-10 Soft-Skill-Description">
                            I can help others when needed, accept others' ideas and feedback, and interact properly with different personalities in different situations.
                        </div>
                        <div className="col-xs-2"></div>

                        <div className="col-xs-12 row Soft-Skill-Row">
                            <div className="col-xs-6" style={{ padding: 0 }}>Empathy</div>
                            <div className="col-xs-6 Skill-Rating-Wrapper" style={{ padding: 0 }}><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        </div>
                        <div className="col-xs-10 Soft-Skill-Description">
                            I can put myself in others' shoes in order to better understand their needs and issues, whether it be an application user, a team member or a client.
                        </div>
                        <div className="col-xs-2"></div>

                        <div className="col-xs-12 row Soft-Skill-Row">
                            <div className="col-xs-6" style={{ padding: 0 }}>Self management</div>
                            <div className="col-xs-6 Skill-Rating-Wrapper" style={{ padding: 0 }}><img className="Skill-Rating" src="media/5-Stars.png" alt="star"></img></div>
                        </div>
                        <div className="col-xs-10 Soft-Skill-Description">
                            I can manage multiple tasks with different contexts, thanks to a personal management system that keeps track of what has been done and what to do.
                        </div>
                        <div className="col-xs-2"></div>
                    </div>
                </div>
                <div className="col-xs-0 col-md-1"></div>
            </div>
        </section>
    )
}

export default AboutSection