import './App.css';
import './Animations.css';
import { useEffect, useState } from 'react';
import AboutSection from './components/About-Section';
import ProjectsSection from './components/Projects-Section';

const TECH_IMAGES_AMOUNT = 24;
const TECH_IMAGE_ID_BASE = "tech-image-";

function App() {
  const [shouldFixHeader, setShouldFixHeader] = useState(false)

  useEffect(() => {
    document.addEventListener("scroll", e => {
      setShouldFixHeader(window.scrollY > window.innerHeight)
    });

    const invisibleElements = document.getElementsByClassName("Invisible")
    Array.from(invisibleElements).forEach(elem => {
      new IntersectionObserver((entries, observer) => {
        const entry = entries[0]
        if (entries[0].isIntersecting) {
          const animationName = entry.target.getAttribute("data-animation")
          elem.classList.add(animationName)
          elem.classList.remove("Invisible")
          observer.disconnect()
        }
      }, { root: null, rootMargin: "0px", threshold: 0.5 }).observe(elem)
    })
  }, [])

  function showImageWithAnimation(index) {
    const elem = document.getElementById(TECH_IMAGE_ID_BASE + index)
    const delay = (Math.pow(index + 1, 0.35) * 1000) - 1000

    setTimeout(() => {
      elem.classList.remove("Hidden")
      elem.classList.add("flip-in-ver-right")
    }, delay)
  }

  return (
    <div id="app" className="App">
      <div id="home">
        <div style={{ flex: 2 }}></div>
        <div id="self-description-1">
          Hello! I'm <span id="name">Loïc Dalian</span>.<br />
          I'm a <span id="profession">full stack web developer</span>, with a preference for the frontend.<br />
          This website will let you know more about <a href="#about" className="Description-About-Link">who I am</a>&#8203;
          and <a href="#projects" className="Description-Projects-Link">what I do</a>.<br />
        </div>
        <div style={{ flex: 1 }}></div>
        <div id="tech-images" className="row">
          {Array.from(Array(TECH_IMAGES_AMOUNT)).map((value, index) =>
            <div key={index} className="col-xs-6 col-sm-4 col-md-3 col-lg-2 Tech-Image-Wrapper">
              <img id={TECH_IMAGE_ID_BASE + index} src={`media/tech_img_${index}-min.png`}
                className="Hidden" onLoad={() => showImageWithAnimation(index)} alt={`tech img ${index}`} />
            </div>
          )}
        </div>
        <div style={{ flex: 2 }}></div>
      </div>
      <div className={"App-header HideIfXsMax " + (shouldFixHeader ? "Fixed slide-in-top" : "")}>
        <span><a href="#home">HOME</a></span>
        <span><a href="#about">ABOUT</a></span>
        <span><a href="#projects">PROJECTS</a></span>
        <span><a href="#contact">CONTACT</a></span>
      </div>
      <AboutSection></AboutSection>
      <ProjectsSection></ProjectsSection>
      <section id="contact">
        <div className="Section-Title Green Invisible" data-animation="slide-in-left">CONTACT</div>
        <div className="Section-Title-Bar Invisible" data-animation="slide-in-right" style={{ background: "#04c2c9" }}></div>
        <div className="row">
          <div className="HideIfXsMax col-sm-4">
            <svg className="Contact-Chevron Invisible" data-animation="fade-in" xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none" /><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z" /></svg>
          </div>
          <div className="col-xs-12 col-sm-4 Contact-Text Invisible" data-animation="fade-in">
            Interested in working together ?<br />
            You can find my contact information here :
          </div>
          <div className="HideIfXsMax col-sm-4">
            <svg className="Contact-Chevron Invisible" data-animation="fade-in" xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none" /><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12l4.58-4.59z" /></svg>
          </div>
        </div>
        <div className="col-xs-12">
          <a href="https://www.linkedin.com/in/lo%C3%AFc-dalian-123006144/" target="blank">
            <img src="media/linkedin.png" className="Invisible" alt="LinkedIn" data-animation="wobble-hor-bottom"></img>
          </a>
        </div>
      </section>
    </div>
  );
}

export default App;
